import { GET_WORKOUTS, GET_WORKOUT, DELETE_WORKOUT } from "../actions/types";

const initialState = {
  workouts: [],
  workout: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_WORKOUTS:
      return {
        ...state,
        workouts: action.payload
      };
    case GET_WORKOUT:
      return {
        ...state,
        workout: action.payload
      };
    case DELETE_WORKOUT:
      return {
        ...state,
        workouts: state.workouts.filter(
          workout => workout.id !== action.payload
        )
      };
    default:
      return state;
  }
}
