import React, { Component } from "react";
import Workout from "./workouts/Workout";
import { connect } from "react-redux";
import { getWorkouts } from "../actions/workoutActions";
import PropTypes from "prop-types";
import "../../src/App.css";
class Dashboard extends Component {
  state = {
    dayIn: undefined
  };

  componentDidMount() {
    let day = new Date();
    let today = day.getDay();
    this.props.getWorkouts();
    this.setState({ dayIn: today });
  }
  addDay = e => {
    const { dayIn } = this.state;
    e.preventDefault();
    if (dayIn >= 6) {
      this.setState({ dayIn: 0 });
    } else {
      this.setState({ dayIn: dayIn + 1 });
    }
  };
  removeDay = e => {
    const { dayIn } = this.state;
    e.preventDefault();
    if (dayIn <= 0) {
      this.setState({ dayIn: 6 });
    } else {
      this.setState({ dayIn: dayIn - 1 });
    }
  };
  render() {
    const { dayIn } = this.state;
    const { workouts } = this.props.workout;

    let dDay;
    switch (dayIn) {
      case 0:
        dDay = "Sunday";
        break;
      case 1:
        dDay = "Monday";
        break;
      case 2:
        dDay = "Tuesday";
        break;
      case 3:
        dDay = "Wednesday";
        break;
      case 4:
        dDay = "Thursday";
        break;
      case 5:
        dDay = "Friday";
        break;
      case 6:
        dDay = "Saturday";
        break;

      default:
        break;
    }
    const workoutDay = workouts.filter(workout => workout.day === dDay);

    return (
      <div className="container">
        <div className="d-flex justify-content-between">
          <i
            className="fas fa-angle-left fa-5x angleclick"
            title="previous day"
            onClick={this.removeDay}
          />{" "}
          <h1>Workout for {dDay}</h1>
          <i
            className="fas fa-angle-right  fa-5x angleclick "
            title="previous day"
            onClick={this.addDay}
          />
        </div>

        {workoutDay.map(workout => (
          <Workout key={workout.id} workout={workout} />
        ))}
      </div>
    );
  }
}

Dashboard.propTypes = {
  workout: PropTypes.object.isRequired,
  getWorkouts: PropTypes.func.isRequired
};
const mapStateToProps = state => ({
  workout: state.workout
});

export default connect(
  mapStateToProps,
  { getWorkouts }
)(Dashboard);
