import React, { Component } from "react";
import { getWorkout, createWorkout } from "../../actions/workoutActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import classnames from "classnames";

class UpdateWorkout extends Component {
  state = {
    id: "",
    workoutName: "",
    description: "",
    reps: "",
    sets: "",
    rest: "",
    weight: "",
    day: "",
    minOrSec: "0",
    errors: {}
  };
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
    const {
      id,
      workoutName,
      description,
      reps,
      sets,
      rest,
      weight,
      day
    } = nextProps.workout;

    this.setState({
      id,
      workoutName,
      description,
      reps,
      sets,
      rest,
      weight
    });
    switch (day) {
      case "Sunday":
        this.setState({ day: "0" });
        break;
      case "Monday":
        this.setState({ day: "1" });
        break;
      case "Tuesday":
        this.setState({ day: "2" });
        break;
      case "Wednesday":
        this.setState({ day: "3" });
        break;
      case "Thursday":
        this.setState({ day: "4" });
        break;
      case "Friday":
        this.setState({ day: "5" });
        break;
      case "Saturday":
        this.setState({ day: "6" });
        break;

      default:
        break;
    }
  }

  componentDidMount() {
    const { id } = this.props.match.params;
    this.props.getWorkout(id, this.props.history);
  }

  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  onSubmit = e => {
    e.preventDefault();
    const { createWorkout, history } = this.props;
    const {
      id,
      workoutName,
      description,
      reps,
      sets,
      rest,
      weight,
      day,
      minOrSec
    } = this.state;

    const newWorkout = {
      id,
      workoutName,
      description,
      reps,
      sets,
      rest,
      weight,
      day,
      minOrSec
    };
    createWorkout(newWorkout, history);
  };
  render() {
    const {
      workoutName,
      description,
      reps,
      sets,
      rest,
      weight,
      day,
      minOrSec,
      errors
    } = this.state;
    return (
      <div className="container">
        <div className="card mb-3">
          <div className="card-header">
            <h4>
              {" "}
              <i className="fas fa-dumbbell" style={{ paddingRight: ".3em" }} />
              Update Workout
              <i
                className="fas fa-plus-square"
                style={{
                  float: "right",

                  paddingLeft: "1em",
                  color: "#8fcadd"
                }}
              />
            </h4>
          </div>

          <div className="card-body">
            <form onSubmit={this.onSubmit}>
              <div className="form-group">
                <input
                  type="text"
                  className={classnames("form-control form-control-lg", {
                    "is-invalid": errors.workoutName
                  })}
                  name="workoutName"
                  placeholder="Enter Workout..."
                  value={workoutName}
                  onChange={this.onChange}
                />
                {errors.workoutName && (
                  <div className="invalid-feedback">{errors.workoutName}</div>
                )}
              </div>

              <h6> Description</h6>
              <div className="form-group">
                <textarea
                  className={classnames("form-control form-control-lg", {
                    "is-invalid": errors.description
                  })}
                  placeholder="Workout Description"
                  name="description"
                  value={description}
                  onChange={this.onChange}
                />
                {errors.description && (
                  <div className="invalid-feedback">{errors.description}</div>
                )}
              </div>
              <h6>Repetitions</h6>
              <div className="form-group">
                <input
                  type="number"
                  className="form-control form-control-lg"
                  name="reps"
                  placeholder="Enter number of reps"
                  value={reps}
                  onChange={this.onChange}
                />
              </div>
              <h6>Sets</h6>
              <div className="form-group">
                <input
                  type="number"
                  className="form-control form-control-lg"
                  name="sets"
                  placeholder="Enter number of sets"
                  value={sets}
                  onChange={this.onChange}
                />
              </div>
              <div className="d-flex justify-content-start">
                <h6 className="radio pr-3">Rest time</h6>
                <div className="radio pr-3">
                  <label>
                    <input
                      type="radio"
                      name="minOrSec"
                      value="0"
                      checked={minOrSec === "0"}
                      onChange={this.onChange}
                    />
                    Minutes
                  </label>
                </div>
                <div className="radio pr-3">
                  <label>
                    <input
                      type="radio"
                      name="minOrSec"
                      value="1"
                      checked={minOrSec === "1"}
                      onChange={this.onChange}
                    />
                    Seconds
                  </label>
                </div>
              </div>
              <div className="form-group">
                <input
                  type="number"
                  className="form-control form-control-lg"
                  name="rest"
                  placeholder="Enter rest time"
                  value={rest}
                  onChange={this.onChange}
                />
              </div>

              <h6> Weight </h6>
              <div className="form-group">
                <input
                  type="number"
                  className="form-control form-control-lg"
                  name="weight"
                  placeholder="Enter weight amount"
                  value={weight}
                  onChange={this.onChange}
                />
              </div>

              <div className="form-group">
                <label htmlFor="day">Select day</label>
                <select
                  name="day"
                  value={day}
                  id="day"
                  onChange={this.onChange}
                  className={classnames("form-control form-control-lg", {
                    "is-invalid": errors.day
                  })}
                >
                  <option> Choose a day </option>
                  <option value="0">Sunday</option>
                  <option value="1">Monday</option>
                  <option value="2">Tuesday</option>
                  <option value="3">Wednesday</option>
                  <option value="4">Thursday</option>
                  <option value="5">Friday</option>
                  <option value="6">Saturday</option>
                </select>
                {errors.day && (
                  <div className="invalid-feedback">{errors.day}</div>
                )}
              </div>
              <input
                type="submit"
                value="Update Workout"
                className="btn btn-outline-secondary btn-block"
              />
            </form>
          </div>
        </div>
      </div>
    );
  }
}
UpdateWorkout.propTypes = {
  getWorkout: PropTypes.func.isRequired,
  workout: PropTypes.object.isRequired,
  createWorkout: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  workout: state.workout.workout,
  errors: state.errors
});
export default connect(
  mapStateToProps,
  { getWorkout, createWorkout }
)(UpdateWorkout);
