import React, { Component } from "react";
import { connect } from "react-redux";
import { login } from "../../actions/secutiryActions";
import PropTypes from "prop-types";
import classnames from "classnames";
import "../../App.js";

class Login extends Component {
  state = {
    username: "",
    password: "",
    errors: {}
  };
  componentDidMount() {
    if (this.props.security.validToken) {
      this.props.history.push("/dashboard");
    }
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.security.validToken) {
      this.props.history.push("/dashboard");
    }
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }
  onSubmit = e => {
    e.preventDefault();
    const { username, password } = this.state;
    const { login } = this.props;
    const loginRequest = {
      username,
      password
    };
    login(loginRequest);
  };
  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  render() {
    const { username, password, errors } = this.state;

    return (
      <div className="container">
        <div className="card text-center mx-auto w-50">
          <div className="card-header">
            <i className="fas fa-user fa-5x" />
            <h3 style={{ color: "#8aacb8" }}> Login</h3>
          </div>
          <div className="card-body">
            <form onSubmit={this.onSubmit}>
              <div className="form-group">
                <label htmlFor="InputEmail1">
                  <i className="fas fa-envelope" /> Email
                </label>
                <input
                  type="email"
                  className={classnames("form-control form-control-lg", {
                    "is-invalid": errors.username
                  })}
                  placeholder="Enter email"
                  name="username"
                  value={username}
                  onChange={this.onChange}
                />

                {errors.username && (
                  <div className="invalid-feedback">{errors.username}</div>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="InputPassword1">
                  <i className="fas fa-unlock-alt" /> Password{" "}
                </label>
                <input
                  type="password"
                  className={classnames("form-control form-control-lg", {
                    "is-invalid": errors.password
                  })}
                  placeholder="Password"
                  name="password"
                  value={password}
                  onChange={this.onChange}
                />
                {errors.password && (
                  <div className="invalid-feedback">{errors.password}</div>
                )}
              </div>

              <input
                type="submit"
                className="btn btn-outline-secondary btn-lg mt-4 "
              />
            </form>
          </div>
        </div>
      </div>
    );
  }
}
Login.propTypes = {
  login: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired,
  security: PropTypes.object.isRequired
};
const mapStatetoProps = state => ({
  security: state.security,
  errors: state.errors
});

export default connect(
  mapStatetoProps,
  { login }
)(Login);
