import axios from "axios";
import { GET_ERRORS, GET_WORKOUTS, GET_WORKOUT, DELETE_WORKOUT } from "./types";

export const createWorkout = (workout, history) => async dispatch => {
  try {
    await axios.post("/api/workout", workout);
    history.push("/dashboard");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const getWorkouts = () => async dispatch => {
  const res = await axios.get("/api/workout/workouts");
  dispatch({
    type: GET_WORKOUTS,
    payload: res.data
  });
};
export const getWorkout = (id, history) => async dispatch => {
  try {
    const res = await axios.get(`/api/workout/${id}`);
    dispatch({
      type: GET_WORKOUT,
      payload: res.data
    });
  } catch (err) {
    history.push("/dashboard/");
  }
};
export const deleteWorkout = id => async dispatch => {
  if (window.confirm("Delete workout permanently?")) {
    await axios.delete(`/api/workout/${id}`);
    dispatch({
      type: DELETE_WORKOUT,
      payload: id
    });
  }
};
