import React, { Component } from "react";
import "../../App.js";
import { createNewUser } from "../../actions/secutiryActions";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import classnames from "classnames";

class Register extends Component {
  state = {
    username: "",
    fullName: "",
    password: "",
    confirmPassword: "",
    errors: {}
  };
  componentDidMount() {
    if (this.props.security.validToken) {
      this.props.history.push("/dashboard");
    }
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }
  onChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };
  onSubmit = e => {
    e.preventDefault();
    const { username, fullName, password, confirmPassword } = this.state;
    const { createNewUser, history } = this.props;
    const newUser = {
      username,
      fullName,
      password,
      confirmPassword
    };
    createNewUser(newUser, history);
  };
  render() {
    const {
      username,
      fullName,
      password,
      confirmPassword,
      errors
    } = this.state;

    return (
      <div className="container">
        <div className="card text-center mx-auto w-50">
          <div className="card-header">
            <i className="fas fa-user-plus fa-5x" />
            <h3 style={{ color: "#8aacb8" }}> Sign Up</h3>
          </div>
          <div className="card-body">
            <form onSubmit={this.onSubmit}>
              <div className="form-group">
                <label htmlFor="InputName1">
                  <i className="fas fa-file-signature" /> Name
                </label>
                <input
                  type="text"
                  className={classnames("form-control form-control-lg", {
                    "is-invalid": errors.fullName
                  })}
                  placeholder="Enter Name"
                  name="fullName"
                  value={fullName}
                  onChange={this.onChange}
                />
                {errors.fullName && (
                  <div className="invalid-feedback">{errors.fullName}</div>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="InputEmail1">
                  <i className="fas fa-envelope" /> Email
                </label>
                <input
                  type="email"
                  className={classnames("form-control form-control-lg", {
                    "is-invalid": errors.username
                  })}
                  placeholder="Enter email"
                  name="username"
                  value={username}
                  onChange={this.onChange}
                />
                {errors.username && (
                  <div className="invalid-feedback">{errors.username}</div>
                )}
              </div>
              <div className="form-group">
                <label htmlFor="InputPassword1">
                  <i className="fas fa-unlock-alt" /> Password{" "}
                </label>
                <input
                  type="password"
                  className={classnames("form-control form-control-lg", {
                    "is-invalid": errors.password
                  })}
                  placeholder="Password"
                  name="password"
                  value={password}
                  onChange={this.onChange}
                />
                {errors.password && (
                  <div className="invalid-feedback">{errors.password}</div>
                )}
              </div>
              <div className="form-group">
                <input
                  type="password"
                  className={classnames("form-control form-control-lg", {
                    "is-invalid": errors.confirmPassword
                  })}
                  placeholder="Confirm Password"
                  name="confirmPassword"
                  value={confirmPassword}
                  onChange={this.onChange}
                />
                {errors.confirmPassword && (
                  <div className="invalid-feedback">
                    {errors.confirmPassword}
                  </div>
                )}
              </div>
              <input
                type="submit"
                className="btn btn-outline-secondary btn-lg mt-4 "
              />
            </form>
          </div>
        </div>
      </div>
    );
  }
}
Register.propTypes = {
  createNewUser: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired,
  security: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors,
  security: state.security
});

export default connect(
  mapStateToProps,
  { createNewUser }
)(Register);
